$darkblue: #2c40c8;
$white: #fff;
$aqua: #00dab9;
$lightblue: #08bab1;
$green: #109836;

$link-color: $darkblue;
$btn-white_bg: $white;
$btn-aqua_bg: $aqua;
$btn-aqua_color: $white;

.container {
  max-width: 1180px;
  width: 100%;
  margin: 0 auto; }

a.button {
  display: inline-block;
  text-decoration: none;
  border-radius: 30px;
  box-shadow: 0 20px 20px 0 rgba(18,29,132,.26); }

.button_white {
  padding: 20px 30px;
  background-color: $btn-white_bg;
  color: $link-color;
  &:hover {
    color: $btn-white_bg;
    background-color: $link-color; }
  &:active {
    color: $btn-white_bg;
    background-color: darken($link-color, 20%); } }

.button_aqua {
  padding: 20px 80px;
  background-color: $btn-aqua_bg;
  color: $btn-aqua_color;
  &:hover {
    background-color: darken($btn-aqua_bg, 10%); }
  &:active {
    background-color: darken($btn-aqua_bg, 20%); } }

* {
  margin: 0;
  padding: 0;
  font-size: 18px;
  line-height: 1.4em; }

a {
  transition: all 0.3s ease-in-out; }

.logo {
  padding-top: 10px;
  img {
    width: 150px; } }
.svg {
  position: absolute;
  left: 0;
  width: 100%;
  height: 30px; }

.header {
  position: relative;
  // box-shadow: 0 20px 0 40px rgba(30,180,222,.15)
  background-image: url(../blocks/header/bg.png);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  margin-bottom: 100px;
  .svg {
    bottom: -1px; }
  .logo {
    margin-bottom: 100px; }
  a.button {
    margin-bottom: 250px; }
  &__text {
    width: 60%;
    color: white;
    margin-bottom: 45px;
    h1 {
      font-size: 2.5em;
      margin-bottom: 30px; } }
  &__img {
    img {
      position: absolute;
      bottom: 0;
      left: 50%;
      max-width: 620px;
      width: 100%;
      min-width: 250px; } } }

.title {
  font-size: 2.8em;
  font-weight: 800;
  text-align: center;
  margin-bottom: 75px; }


