@mixin triangle ($color: #fff, $width: 10px) {
  content: "";
  position: absolute;
  width: $width;
  height: $width;
  top: (-$width);
  left: 50%;
  background: #fff;
  border-left: 4px solid $color;
  border-top: 4px solid $color;
  transform: translateX(-50%) rotate(45deg); }


.about-product, .team {
  margin-bottom: 80px;
  &__items {
    display: flex; }
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-basis: 33%;
    &:not(:last-child) {
      margin-right: 50px; }
    &:nth-child(1) {
      .about-product__info {
        border-color: $lightblue;
        h5 {
          color: $lightblue; }
        &::before {
          @include triangle($lightblue); } } }
    &:nth-child(2) {
      .about-product__info {
        border-color: $green;
        h5 {
          color: $green; }
        &::before {
          @include triangle($green); } } }
    &:nth-child(3) {
      .about-product__info {
        border-color: $darkblue;
        h5 {
          color: $darkblue; }
        &::before {
          @include triangle($darkblue); } } } }
  &__img {
    margin-bottom: 35px;
    img {
      width: 150px; } }
  &__info {
    position: relative;
    border-radius: 5px;
    border: 4px solid;
    padding: 20px 35px;
    text-align: center;
    h5 {
      font-size: 1.3em;
      margin-bottom: 10px; } } }

.notify-block {
  padding: 80px 0;
  background-image: linear-gradient(to top right, #34e39b, #2c93c6, #39439d);
  margin-bottom: 100px;
  .title {
    color: $white; } }
.notify-form {
  text-align: center;
  &__input {
    display: block;
    width: 100%;
    max-width: 340px;
    background: transparent;
    color: white;
    border: none;
    border-bottom: 1px solid $aqua;
    text-align: center;
    padding: 15px;
    margin: 0 auto;
    margin-bottom: 40px;
    &::placeholder {
      color: rgba(255,255,255,.6); }
    &:focus {
      outline: none; } } }

.team {
  margin-bottom: 100px;
  &__items {
    justify-content: space-between; }
  &__img {
    img {
      width: 220px;
      border-radius: 50%; } }
  &__info {
    padding: 0;
    border: none; } }

.button-block {
  text-align: center;
  position: relative;
  background-image: url(../blocks/footer/bg.png);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  padding-top: 150px;
  padding-bottom: 20px;
  .round-block {
    position: absolute;
    top: 30px;
    width: 100%;
    transform: scale(1, -1); }
  *:not(.button) {
    color: $white; }
  .button {
    margin-bottom: 150px; } }
.footer {
  &__text {
    display: flex;
    justify-content: space-between;
